import { Star } from 'lucide-react';
import Image from 'next/image';

type Product = {
  id: string;
  name: string;
  storeName: string;
  rating: number;
  reviews: number;
  price: number;
  originalPrice: number;
  discount: number;
  image: string;
  isFavorite?: boolean;
};

interface ProductCardProps {
  product: Product;
}

export default function ProductCard({ product }: ProductCardProps) {
  const {
    storeName,
    name,
    price,
    originalPrice,
    discount,
    image,
    rating,
    reviews,
    isFavorite,
  } = product;

  return (
    <div className="bg-white rounded-2xl p-8 shadow-sm mb-6">
      <div className="flex gap-12">
        <div className="relative w-[300px] h-[300px]">
          <Image
            src={image}
            alt={name}
            fill
            className="rounded-xl object-cover"
          />
        </div>
        <div className="flex flex-col justify-between flex-1 py-4">
          <div>
            <div className="flex items-center justify-between mb-2">
              <p className="text-lg text-gray-900">{storeName}</p>
              <div className="flex items-center gap-4 bg-orange-100 px-4 py-2 rounded-full">
                <Star className="w-8 h-8 fill-yellow-400 text-yellow-400" />
                <span className="font-semibold text-2xl">{rating}</span>
                <span className="text-gray-600 text-2xl">({reviews})</span>
              </div>
            </div>
            <h3 className="text-3xl font-semibold mb-6">{name}</h3>
            <div className="flex items-center gap-3">
              <span className="text-4xl font-bold">₹{price}</span>
              <span className="text-xl text-gray-500 line-through">₹{originalPrice}</span>
              <span className="bg-red-500 text-white px-3 py-1 rounded-lg">{discount}% OFF</span>
            </div>
          </div>
          <button className="bg-black text-white py-4 rounded-xl hover:bg-gray-800 transition-colors text-lg w-48">
            Add to Cart
          </button>
        </div>
        <div className="ml-auto">
          <div className="w-[195px] h-[51px] flex items-center justify-center bg-[#FFE1CE] rounded-[30px] gap-[10px] hover:cursor-pointer">
            <Star className="w-8 h-8 fill-yellow-400 text-yellow-400" />
            <span className="font-semibold text-xl">
              {rating} ({reviews})
            </span>
          </div>
          <div className="w-8 h-8 mt-[50px] ml-auto hover:cursor-pointer">
            <Image
              src={isFavorite ? '/heart-filled.svg' : '/Vector(4).svg'}
              alt="Favorite"
              width={32}
              height={32}
            />
          </div>
        </div>
      </div>
    </div>
  );
}
